const Transactions = () => {
  const [transactions, setTransactions] = useState([]);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchTransactions = async () => {
      try {
        const web3 = new Web3(
          'https://sepolia.infura.io/v3/431809bc32c9490781aea4868ac63d63'
        );
        const contract = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);

        const transactionIds = await contract.methods.getAllTransactionIds().call();
        console.log('Fetched Transaction IDs:', transactionIds);

        const limitedIds = transactionIds.slice(0, 10);

        const fetchedTransactions = await Promise.all(
          limitedIds.map(async (id) => {
            const details = await contract.methods.getTransactionDetails(id).call();
            return {
              id,
              core: details[0],
              compliance: details[1],
            };
          })
        );

        setTransactions(fetchedTransactions);
      } catch (err) {
        console.error('Error fetching transactions:', err.message);
        setError('Failed to fetch transactions.');
      }
    };

    fetchTransactions();
  }, []);

  const formatBigInt = (value, decimals = 18) =>
    Number(value) / Math.pow(10, decimals);

  return (
    <div className="transactions-container">
      <Helmet>
        <title>ChainGuardian AI - Transactions</title>
        <meta property="og:title" content="ChainGuardian AI Transactions" />
      </Helmet>

      {/* NAVBAR */}
      <Navbar8
        link1={<Fragment><span>Home</span></Fragment>}
        link2={<Fragment><span>Dashboard</span></Fragment>}
        link3={<Fragment><span>Regulatory Library</span></Fragment>}
        link4={<Fragment><span>AI Assistant</span></Fragment>}
        action1={<Fragment><span>Sign In</span></Fragment>}
        action2={<Fragment><span>Get Started</span></Fragment>}
      />

      {/* TRANSACTIONS */}
      <div className="transactions-content">
        <h1 className="transactions-heading">Blockchain Transactions</h1>
        {error && <p className="transactions-error">{error}</p>}
        {transactions.length === 0 ? (
          <p className="transactions-empty">No transactions found.</p>
        ) : (
          <div className="transactions-grid">
            {transactions.map(({ id, core, compliance }) => (
              <div className="transaction-card" key={id}>
                <img
                  src="https://via.placeholder.com/300" // Replace with dynamic images if available
                  alt="Transaction Thumbnail"
                  className="transaction-card-image"
                />
                <div className="transaction-card-content">
                  <h3>Transaction ID</h3>
                  <p className="transaction-id">{id.slice(0, 15)}...</p>
                  <div className="transaction-details">
                    <p><strong>Sender:</strong> {core.sender.slice(0, 10)}...</p>
                    <p><strong>Receiver:</strong> {core.receiver.slice(0, 10)}...</p>
                    <p>
                      <strong>Amount:</strong> {formatBigInt(core.amount)} {core.currency_type}
                    </p>
                    <p><strong>Gas Fee:</strong> {formatBigInt(core.gas_fee)} ETH</p>
                    <p>
                      <strong>Timestamp:</strong> {new Date(Number(core.timestamp) * 1000).toLocaleString()}
                    </p>
                    <p><strong>Purpose:</strong> {compliance.purpose_of_transaction || 'N/A'}</p>
                    <p>
                      <strong>AML/KYC Verified:</strong> {compliance.aml_kyc_verified ? 'Yes' : 'No'}
                    </p>
                    <p><strong>Region:</strong> {compliance.regulatory_region || 'N/A'}</p>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* FOOTER */}
      <Footer4
        link1={<Fragment><span>Dashboard</span></Fragment>}
        link2={<Fragment><span>Regulatory Library</span></Fragment>}
        link3={<Fragment><span>AI Assistant</span></Fragment>}
        link4={<Fragment><span>Help/FAQ</span></Fragment>}
        link5={<Fragment><span>Settings</span></Fragment>}
        termsLink={<Fragment><span>Terms of Use</span></Fragment>}
        cookiesLink={<Fragment><span>Cookies Policy</span></Fragment>}
        privacyLink={<Fragment><span>Privacy Policy</span></Fragment>}
      />
    </div>
  );
};

export default Transactions;